import React, { useEffect, useState } from 'react';
import { PageProps } from 'gatsby';
import { Loader } from '@googlemaps/js-api-loader';

interface AboutProps {
  data: any[];
}

const About: React.FC<AboutProps> = (props) => {
  useEffect(() => {
    const loader = new Loader({
      apiKey: 'AIzaSyBI_6Bn1nUDu3o677tqEmmW1KV7UhnKkCU',
      version: 'weekly'
    });

    loader.load().then(() => {
      const { google } = window as any;

      if (
        !document.getElementById('map') ||
        !google ||
        !google.maps ||
        !google.maps.Map
      )
        return;

      const lat: number = 35.67612894074747;
      const lng: number = 139.71010032475465;

      const map: any = new (window as any).google.maps.Map(
        document.getElementById('map') as HTMLElement,
        {
          center: {
            lat,
            lng
          },
          zoom: 16
        }
      );

      const image: string = 'https://daruma-inv.com/pin.png';

      const beachMarker: any = new google.maps.Marker({
        position: { lat, lng },
        map,
        icon: {
          url: image,
          scaledSize: new google.maps.Size(40, 40)
        }
      });
    });
  });

  return !props.data ? (
    <></>
  ) : (
    <section id='about' className='bg-gray'>
      <div className='container'>
        <h1></h1>
        <div className='about-background bg-white'>
          <div className='row'>
            <div className='col-10 offset-1 col-lg-8 offset-lg-2'>
              <table className='table'>
                <tbody>
                  {props.data.map((d: any, i: number) => (
                    <tr key={i}>
                      <th scope='row'>{d.key}</th>
                      <td>
                        <ul>
                          <li
                            dangerouslySetInnerHTML={{
                              __html: d.value.replace(/\n/g, '<br />')
                            }}
                          />
                        </ul>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
          <div
            id='map'
            style={{
              width: '100%',
              height: 360,
              filter: 'grayscale(100%)'
            }}
          />
        </div>
      </div>
    </section>
  );
};

export default About;
